* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

body {
  font-family:sans-serif;
  background-color: #212426;
}

.app {
  padding: 4.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

h1 {
  font-size: 4rem;
  color:rgb(224,224,244);
  width: fit-content;
}

.search {
  width: 50%;
  margin: 5rem 0 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  background: #1f2123;
  box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
}

.search input {
  flex: 1;
  border: none;
  font-size: 2rem;
  font-family:Arial, Helvetica, sans-serif;
  font-weight: 100;
  padding-right: 1rem;
  outline: none;
  color: #a1a1a1;
  background: #1f2123;
}

.search img {
  width: 35px;
  height: 35px;
  cursor: pointer;
}


.container {
 /* width: 100%;
  margin-top: 3rem;*/
  display: grid;
  grid-template-columns: repeat(4,120px);
  grid-auto-rows:(150px, auto);
  grid-column-gap: 150px;
  row-gap:20px;
  justify-items: center;
}

 .search button {
  padding: 20px 40px;
  border-radius: 0.5rem;
  margin-left: 15px;
  color: #a1a1a1;
  font-family:sans-serif;
  font-weight: 900;
  letter-spacing: 0.75px;
  font-size: 1.25rem;
  cursor: pointer;
  background: #1f2123;
  -webkit-box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
  box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
} 

.empty {
  width: 100%;
  margin-top: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.empty h2 {
  font-size: 1.25rem;
  color: #f9d3b4;
  font-family: var(--font-raleway);
}

.item {
  width: 200px; 
  margin: 1.3rem;
  position: relative;
  border-radius: 12px;
  border: none;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}


.top {
  position: absolute;
  padding: 16px;
  width: 100%;
  opacity: 0;
  top: 0;
  color: #f9d3b4;
}

.item:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.item div:nth-of-type(2) {
  width: 100%;
  height: 150px;
}

.item .img {
  height: 200px; 
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.item div:nth-of-type(2) {
  background-color: #343739;
  padding: 16px 24px 24px 24px;
  bottom: 0;
  right: 0;
  left: 0;
}

.item div:nth-of-type(2) span {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #f0f0f0;
}

.item div:nth-of-type(2) h3 {
  margin-top: 5px;
  font-family: "Roboto Slab", serif;
  color: #f9d3b4;
}

.item:hover div:nth-of-type(2) {
  height: 100%;
  opacity: 0.3;
}


.item:hover div:nth-of-type(1) {
  opacity: 1;
}

@media screen and (max-width: 600px) {
  .app {
    padding: 4rem 2rem;
  }

  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .search input {
    font-size: 1rem;
  }

  .search img {
    width: 20px;
    height: 20px;
  }
}

@media screen and (max-width: 400px) {
  .app {
    padding: 4rem 1rem;
  }

  h1 {
    font-size: 2rem;
  }

  .container {
    margin-top: 2rem;
  }

  .item {
    width: "100%";
    margin: 1rem;
  }
}
.text-container {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 10px;
  height: 10px;
}